import '../styles/init.scss'; // Import any styles as this includes them in the build.

Hooks.once("ready", (app, html, data) => {
    console.log('Actor Studio');
    alert('b');

    // Find the chat sidebar tab
    const chatTab = html.find('.chat .tabs .item[data-tab="chat"]');

    // Create a new button element
    const button = $('<button class="my-button">Test Button</button>');

    // Append the button to the chat sidebar tab
    chatTab.append(button);

    // Add a click event listener to the button
    button.on('click', () => {
        alert('Button clicked!');
    });
});

